// mixing
$pixelBase: 16;
@mixin font-size($sizeValue: 1.6) {
  font-size: ($sizeValue * 10) + px;
  font-size: (($sizeValue * 10) / $pixelBase) + rem; }


@mixin w-size($sizeValue: 0.6) {
  width: (($sizeValue * 1) / $pixelBase) + em; }

@mixin h-size($sizeValue: 1.6) {
  height: (($sizeValue * 1) / $pixelBase) + em; }



//after o before img
@mixin afterimg {
    background-repeat: no-repeat;
    background-position: center center;
    background-size: inherit;
    display: inline-block;
    text-align: center;
    @include MQ(M) {
      background-size: 100%; }
    @include MQ(L) {
      background-size: contain; } }
@mixin rotate($rotateValue: 0) {
  transform: rotate($rotateValue + deg); }



@mixin point($canvas) {
    @media screen and (min-width: $value) { @content; }; }

//Mediaqueri
@mixin MQ($canvas) {
    @if $canvas == XS {
        @media only screen and (min-width: $XS) { @content; }; }
    @else if $canvas == S {
        @media only screen and (min-width: $S) { @content; }; }

    @else if $canvas == M {
        @media only screen and (min-width: $M) { @content; }; }

    @else if $canvas == L {
        @media only screen and (min-width: $L) { @content; }; } }

//Boton fantasma
@mixin column($percentage, $float-direction:left) {
    width: 100% * $percentage;
    float: $float-direction; }

//Boton fantasma
@mixin btnGhost {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  text-indent: 200%;
  white-space: nowrap;
  font-size: 0;
  opacity: 0; }

//Boton estados
@mixin press {
  &:hover, &:active, &:focus {
    @content; } }

// Heading site
@mixin heading {
    h2, h3, h4, h5, h6, p, b, small {
        @content; } }

@mixin clearfix-after {
  clear: both; }

@mixin font-smoothing {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }



@mixin center {
  display: flex;
  justify-content: center;
  align-items: center; }
