// text.sass
.title,
.subtitle,
.paragraf {
    display: block; }

.title {
    &_header {
        h2, h3, h4, h5, h6, p {
            display: inline-block; } } }
//.tag_data
.txt {
    &_italic {
        font-style: italic; }
    &_center {
        text-align: center; }
    &_upper {
        text-transform: uppercase; }
    &_capital {
        text-transform: capitalize; }
    &_right {
        text-align: right; }
    &_vertical {
        writing-mode: vertical-rl; }
    &_shadow {
        &--dirty {
            @include heading {
                //text-shadow: 2px 2px 3px rgba($black, .1)
                text-shadow: 1px 2px 0px rgba($white, .5), 2px 3px 0px rgba($black,0.15); } }
        &--halls {
            @include heading {
                text-shadow: rgba($lightPeach, .5) 0px 2px 2px;
                background-color: $darkPeach;
                -webkit-background-clip: text;
                -moz-background-clip: text;
                background-clip: text;
                color: transparent; } } } }

.font {
    &_second {
        @include heading {
            font-family: $second-font; } } }
.left {
    text-align: left; }
.center {
    text-align: center; }
.right {
    text-align: right; }
.justify {
    text-align: justify; }
/* Font sizes */
.fz {
    &_0 {
        font-size: 0; }
    &_25 {
        @include font-size(.4);
        @include heading {
            @include font-size(.4); } }
    &_5 {
        @include font-size(.8);
        @include heading {
            @include font-size(.8); } }
    &_1 {
        @include font-size(1); }
    &_2 {
        @include font-size(2);
        @include heading {
            @include font-size(2); } }
    &_3 {
        @include font-size(2);
        @include MQ(S) {
            @include font-size(3); }
        @include heading {
            @include font-size(2);
            @include MQ(S) {
                @include font-size(3); } } }
    &_5 {
        @include font-size(5);
        @include heading {
            @include font-size(5); } }
    &_7 {
        @include font-size(4.5);
        @include MQ(7) {
            @include font-size(7); }
        @include heading {
            @include font-size(4.5);
            @include MQ(S) {
                @include font-size(7); } } }
    &_12 {
        @include font-size(12);
        @include heading {
            @include font-size(12); } }
    &_13 {
        @include font-size(10);
        @include MQ(S) {
            @include font-size(13); }
        @include heading {
            @include font-size(10);
            @include MQ(S) {
                @include font-size(13); } } } }

