.btn {
    border: none;
    box-shadow: none;
    border-radius: none;
    font-size: 1.4rem;
    font-family: $primary-font;
    padding: 0.8em 2.2em;
    display: block;
    cursor: pointer;
    font-weight: 600;
    -webkit-appearance: inherit;
    //+transition(all 0.3s ease-in-out)

    &--full {
        width: 100%;
        display: block; }
    &_line {
        background-color: transparent;
        border: 2px solid $white;
        color: $white;
        text-align: center;
        @include press {
            background-color: $white; } }
    &_site {
        border-bottom: 2px solid transparent;
        color: $white;
        text-transform: uppercase;
        @include press {
            color: $white; } }
    &_gosth {
        @include btnGhost; } }
