

.content {
    display: block;
    position: relative;
    top: 0;
    width: 100vw;
    height: 100vh;
    &.full {
        @include center; } }
.card {
    z-index: 99;
    width: 100%;
    article {
        &.one {
            @include afterimg;
            background-image: url(../images/site/two_913.png);
            height: 95vh;
            z-index: -1;
            display: block;
            @include center;
            @include MQ(L) {
                background-size: 74%; } }
        &.two,
        &.tree {
            width: 100%;
            @include MQ(M) {
                width: 45%; } }
        &.two {
            background-repeat: no-repeat;
            background-position: 74% center;
            background-size: 206%;
            display: inline-block;
            text-align: center;
            background-image: url(../images/site/seply.png);
            height: 86vh;
            z-index: -1;
            @include center;
            display: inline-flex;
            @include MQ(S) {
                background-size: 150%;
                height: 124vh; }
            @include MQ(M) {
                height: 84vh;
                background-size: 100%;
                background-position: center center; }
            @include MQ(L) {
                background-size: 100%; } }

        &.tree {
            background-repeat: no-repeat;
            background-position: center center;
            background-size: inherit;
            display: inline-block;
            text-align: center;
            background-image: url(../images/site/info.png);
            height: 84vh;
            z-index: -1;
            @include center;
            display: inline-flex;
            @include MQ(S) {
                background-size: 150%;
                height: 124vh; }
            @include MQ(M) {
                height: 84vh;
                background-size: 100%;
                background-position: center center; }
            @include MQ(L) {
                background-size: 80%; } } } }
article {
    &.one {
        .info {
            margin: auto auto 0 0;
            width: 69%;
            height: 58vh;
            @media (min-width: 350px) {
                width: 60%;
                height: 62vh;
                margin: auto 23% 0 auto; }
            @media (min-width: 590px) {
                width: 53%;
                height: 62vh;
                margin: 45% 27% 0 auto; }
            @media (min-width: 676px) {
                width: 45%;
                height: 62vh;
                margin: 43% 29% 0 auto; }
            @media (min-width: 720px) {
                width: 42%;
                height: 62vh;
                margin: 44% 31% 0 auto; }
            @include MQ(S) {
                width: 38%;
                height: 62vh;
                margin: auto auto 0 23%; }
            @media (min-width: 850px) {
                width: 35%;
                height: 62vh;
                margin: 33% 35% 0 auto; }
            @media (min-width: 920px) {
                width: 30%;
                height: 62vh;
                margin: 30% 37% 0 auto; }
            @include MQ(M) {
                width: 29%;
                height: 62vh;
                margin: 22% 37% 0 auto; }
            @media (min-width: 1130px) {
                width: 29%;
                height: 62vh;
                margin: 25% 37% 0 auto; }
            @media (min-width: 1600px) {
                width: 21%;
                height: 62vh;
                margin: 21% 41% 0 auto; } } }
    &.two {
        .info {
            margin: 10% auto 0 auto;
            width: 69%;
            height: 49vh;
            @media (min-width: 350px) {
                width: 66%;
                height: 50vh;
                margin: 27% 6% auto 4%; }
            @media (min-width: 500px) {
                width: 70%;
                height: 32vh;
                margin: 30% 12% auto 10%; }
            @include MQ(S) {
                width: 51%;
                height: 32vh;
                margin: 40% 0% auto 12%; }
 } }            // @media (min-width: 768)
    &.tree {
        .info {
            margin: 10% auto 0 auto;
            width: 69%;
            height: 49vh;
            @media (min-width: 350px) {
                width: 66%;
                height: 50vh;
                margin: 27% 6% auto 4%; }
            @media (min-width: 500px) {
                width: 60%;
                height: 50vh;
                margin: 9% 15% auto 26%; } } } }
